.aboutme_section{display:flex;align-items:center;justify-content:space-evenly;padding:0 5rem}.aboutme_section .content_col{flex:5 1;margin:1rem}.aboutme_section .image_col{flex:3 1;margin:1rem}.aboutme_section .image_col .img_shape{height:15rem;position:absolute;top:-200px}.aboutme_section .image_col .img_main{height:30rem}@media (max-width: 60em){.aboutme_section .image_col .img_main{height:25rem}}@media (max-width: 75.5em){.aboutme_section{padding:0}}@media (max-width: 45em){.aboutme_section{flex-direction:column-reverse}}

.youtube_section{margin:10rem 0;display:flex;flex-direction:column;align-items:center;justify-content:center}.youtube_section .img_shape{height:15rem;position:absolute;top:7rem;right:7rem}@media (max-width: 45em){.youtube_section .img_shape{height:13rem;top:10rem;right:5rem}}.youtube_section .video{margin-bottom:1rem;box-shadow:rgba(0,0,0,0.05) 0px 6px 24px 0px,rgba(0,0,0,0.08) 0px 0px 0px 1px;width:640px !important;height:360px !important}@media (max-width: 45em){.youtube_section .video{width:100% !important}}.youtube_section .para{width:40rem}@media (max-width: 45em){.youtube_section .para{width:100%}}@media (max-width: 45em){.youtube_section{margin:1rem 0}}

.skills_section{margin:5rem 0;display:flex;flex-direction:column;justify-content:space-evenly;padding:5rem;background-color:#ccccccd1;border-radius:2rem;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);box-shadow:#26394d 0px 20px 30px -10px;position:relative;z-index:1000}.skills_section .heading{margin-bottom:1.2rem;text-shadow:2px 2px #aaa}.skills_section .img_shape{position:absolute}.skills_section .img_shape.dimond_img_1{top:250px;right:80px;height:10rem;width:10rem}@media (max-width: 60em){.skills_section .img_shape.dimond_img_1{top:250px;right:80px}}@media (max-width: 53em){.skills_section .img_shape.dimond_img_1{height:8rem;width:8rem;right:10px}}@media (max-width: 45em){.skills_section .img_shape.dimond_img_1{display:none}}.skills_section .img_shape.dimond_img_2{bottom:-800px;left:80px;height:20rem;width:20rem;-webkit-transform:rotate(30deg);transform:rotate(30deg)}@media (max-width: 53em){.skills_section .img_shape.dimond_img_2{height:15rem;width:15rem;bottom:-600px}}@media (max-width: 45em){.skills_section .img_shape.dimond_img_2{display:none}}@media (max-width: 60em){.skills_section{padding:4rem}}@media (max-width: 53em){.skills_section{padding:3rem}}@-webkit-keyframes load{from{width:0%}}@keyframes load{from{width:0%}}.bar{background-color:#ffffff2a;width:100%;padding:2px;border-radius:15px;margin-bottom:1rem;font-size:14px;color:#FFF;font-weight:bold;text-shadow:1px 1px 1px rgba(0,0,0,0.5)}.bar::before{content:attr(data-skill);display:inline-block;padding:5px 0 5px 10px;border-radius:inherit;-webkit-animation:load 2s 0s;animation:load 2s 0s;font-weight:400;background-color:#1f1f1f}.bar.learning::before{width:calc(20% - 10px)}.bar.basic::before{width:calc(40% - 10px)}.bar.intermediate::before{width:calc(60% - 10px)}.bar.advanced::before{width:calc(80% - 10px)}.bar.expert::before{width:calc(100% - 10px)}

.project_section{margin:10rem 2rem}.project_section .row{display:flex}.project_section .row.row_1{align-items:center}@media (max-width: 53em){.project_section .row.row_1{flex-direction:column-reverse;align-items:flex-end}}.project_section .row.row_2{align-items:flex-start;margin-top:10rem}@media (max-width: 68em){.project_section .row.row_2{margin-top:5rem}}@media (max-width: 60em){.project_section .row.row_2{margin-top:1rem}}@media (max-width: 53em){.project_section .row.row_2{flex-direction:column}}.project_section .row .desc_col{flex:5 1}@media (max-width: 68em){.project_section .row .desc_col{flex:1 1}}.project_section .row .desc_col h2,.project_section .row .desc_col h3,.project_section .row .desc_col p{margin-bottom:10px}.project_section .row .desc_col .proj_name{font-size:6rem;color:#0000004d}@media (max-width: 75.5em){.project_section .row .desc_col .proj_name{font-size:5rem}}@media (max-width: 68em){.project_section .row .desc_col .proj_name{font-size:4rem}}@media (max-width: 60em){.project_section .row .desc_col .proj_name{font-size:3.2rem}}@media (max-width: 45em){.project_section .row .desc_col .proj_name{font-size:2.9rem}}.project_section .row .desc_col .proj_designation{font-size:1.5rem}.project_section .row .desc_col .stack_btns button{background-color:#333;border:0;padding:10px 20px;border-radius:20px;margin-right:10px;margin-top:1.5rem;color:#fff}@media (max-width: 60em){.project_section .row .desc_col .stack_btns button{padding:5px 10px;margin-top:1rem}}.project_section .row .desc_col button:hover{cursor:pointer}.project_section .row .img_col{flex:3 1}@media (max-width: 68em){.project_section .row .img_col{flex:1 1}}.project_section .row .img_col .img_shape{height:20rem;position:absolute;right:0px;top:-1000px}.project_section .row .img_col .img_main{width:100%;-webkit-filter:grayscale(100%);filter:grayscale(100%)}@media (max-width: 53em){.project_section .row .img_col .img_main{height:30rem;width:auto}}@media (max-width: 45em){.project_section .row .img_col .img_main{height:26rem}}.project_section .row:hover .proj_name{transition:all 0.2s}.project_section .row:hover .proj_name_1{color:#1083c659}.project_section .row:hover .proj_name_2{color:#c6101059}.project_section .row:hover .img_main{-webkit-filter:grayscale(0%);filter:grayscale(0%);transition:all 0.2s}

.contact_section{display:flex;align-items:center;margin:5rem 0 0 0;padding:5rem;background-color:#ccccccd1;border-radius:1rem;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);box-shadow:#26394d 0px 20px 30px -10px}@media (max-width: 68em){.contact_section{padding:4rem}}@media (max-width: 60em){.contact_section{padding:3rem}}@media (max-width: 53em){.contact_section{padding:2rem;flex-direction:column;align-items:flex-end;text-align:right}}.contact_section .heading{text-shadow:2px 2px #aaa}.contact_section .img_shape{height:25rem;position:absolute;left:-100px;top:-200px}@media (max-width: 53em){.contact_section .img_shape{height:25rem;position:absolute;right:1000px;top:-200px}}@media (max-width: 45em){.contact_section .img_shape{display:none}}.contact_section .content_col{flex:2 1;margin-right:1rem}@media (max-width: 53em){.contact_section .content_col{margin-left:0}}.contact_section .content_col .social_icons{margin-top:5rem}@media (max-width: 53em){.contact_section .content_col .social_icons{margin-top:0}}.contact_section .content_col .social_icons>*{font-size:2.5rem;margin:10px;color:#000}.contact_section .content_col .social_icons>*:hover{color:#333;cursor:pointer}@media (max-width: 53em){.contact_section .content_col .social_icons>*{font-size:2rem}}.contact_section .form_col{flex:3 1;margin-left:1rem}@media (max-width: 53em){.contact_section .form_col{margin-left:0;margin-top:3rem}}.contact_section .form_col input,.contact_section .form_col textarea{width:96%;padding:10px;margin:5px;border-radius:10px;border:1px #aaa solid;font-size:1rem;font-family:'Montserrat', sans-serif}.contact_section .form_col input:focus,.contact_section .form_col textarea:focus{outline:none}@media (max-width: 53em){.contact_section .form_col input,.contact_section .form_col textarea{font-size:0.8rem}}.contact_section .form_col button{margin:10px 5px;background-color:#111827;border:1px solid transparent;border-radius:10px;color:#FFFFFF;font-size:1.125rem;padding:0.75rem 1.2rem;