*,body{margin:0;padding:0}body{font-family:'Montserrat', sans-serif;margin:5rem;width:calc(100% - 10rem);height:calc(100% - 10rem);background-image:url(data:image/jpeg;base64,);background-size:125px 125px;background-repeat:round;background-attachment:fixed;overflow-x:hidden;overflow-y:auto;-webkit-perspective:300px;perspective:300px}@media (max-width: 53em){body{margin:3rem;width:calc(100% - 6rem);height:calc(100% - 6rem)}}@media (max-width: 45em){body{margin:1rem;width:calc(100% - 2rem);height:calc(100% - 2rem)}}html{scroll-behavior:smooth}body::-webkit-scrollbar{width:10px;background-color:#acacac;border:1px #acacac solid}body::-webkit-scrollbar-thumb{background-color:#1f1f1f;border-radius:1rem}.heading{font-size:3rem;margin-bottom:10px}@media (max-width: 53em){.heading{font-size:2.5rem}}.para{margin-bottom:10px;font-size:1.3rem}@media (max-width: 60em){.para{font-size:1.2rem}}@media (max-width: 53em){.para{font-size:1rem}}

